@import "../../globalStyles.css";

.resultsCard {
    display: flex;
    background-color: var(--accent-white);
    border-radius: 32px;
    max-width: 736px;
    width: 100%;
    height: 512px;
}

@media (max-width: 767px) {
    .resultsCard {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: auto;
    }
}
